/*
This module conditionally adds the `a11y.css` file to the head tag. `a11y.css`
highlights HTML elements that are missing certain a11y properties, like an `img`
tag without `alt="..."` for instance. Sometimes it doesn't say exactly what the
problem is, in that case you can use the browser to "inspect element" and then
there will be some CSS pseudo element applied to the element that contains a
description.

If you want to develop without `a11y.css` you can run `npm run dev-no-a11y.css`

In production builds parcel will be able to exclude this entire file from the
final result

Whether or not to add `a11y.css` depends on 2 environment variables:
1) NODE_ENV: This one is set automatically by parcel, defaults to "development"
  except for production builds.
2) A11Y_CSS: If this is set to NO then `a11y.css` isn't included, even in
   development builds
Read more about parcel and environment variables here:
  https://parceljs.org/features/node-emulation/#environment-variables
 */

// @ts-ignore
import a11yCss from "bundle-text:./a11y.css"

// @ts-ignore
if (process.env.NODE_ENV === "development" && process.env.A11Y_CSS !== "NO") {
  document.head.insertAdjacentHTML("beforeend", `<style>\n${a11yCss}\n</style>` );
}
